




































































































































































































































































































































































































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import { AuthModule } from '@/store/modules/authentication.module'
import { DisplayModule } from '@/store/modules/display.module'
import { store } from '@/store/index'
import { clickOutside } from '@/directives/v-click-outside/index'
import SearchResults from '@/components/shared/header/search-results/search-results.vue'
import { ISearchResult } from '@/models/index'
import { envArgs } from '@/env'

@Component({
  components: {
    SearchResults
  },
  directives: {
    clickOutside
  }
})
export default class Header extends Vue {
  blocksShow = {
    menu: false,
    location: false,
    user: false
  };

  searchText = '';

  foundItems: ISearchResult[] = [];

  potentialFoundItems: ISearchResult[] = [
    {
      desc: '',
      number: '45888701',
      manufacturer: 'CITROEN',
      alreadyInBucket: 0
    },
    {
      desc: '',
      number: '95493099',
      manufacturer: 'CITROEN',
      alreadyInBucket: 2
    },
    {
      desc: '',
      number: 'WA54-9309',
      manufacturer: 'WAI',
      alreadyInBucket: 0
    },
    {
      desc: '',
      number: 'WA54-9309-1',
      manufacturer: 'WAI',
      alreadyInBucket: 0
    }
  ];

  toggleRegistrationPopup () {
    store.dispatch('display/toggleRegistration')
  }

  logIn () {
    store.dispatch('auth/login')
  }

  clearSearchInput () {
    this.searchText = ''
    this.foundItems = []
  }

  get loggedIn () {
    return AuthModule.logged
  }

  get userName () {
    return AuthModule.name.length > 10
      ? AuthModule.name.substring(0, 10) + '...'
      : AuthModule.name
  }

  logout () {
    store.dispatch('auth/logout')
  }

  toggleMainMenu () {
    setTimeout(() => {
      this.blocksShow.menu = !this.blocksShow.menu
    })
  }

  closeMainMenu () {
    this.blocksShow.menu = false
  }

  toggleUserMenu () {
    setTimeout(() => {
      this.blocksShow.user = !this.blocksShow.user
    })
  }

  closeUserMenu () {
    this.blocksShow.user = false
  }

  mounted () {
    if (envArgs.isWebComponentBuild) {
      this.$store = store
    }
    this.$store.dispatch('auth/actualizeUser')
  }

  @Watch('searchText')
  onChildChanged (val: string, oldVal: string) {
    if (val.length >= 3) {
      this.foundItems = this.potentialFoundItems
    } else if (val.length <= 3 && oldVal.length >= 3) {
      this.foundItems = []
    }
  }
}
